import logo from './logo.svg';
import './App.scss';
import React, {Componen, useEffect, useState} from 'react';
import axios from 'axios'
import { render } from 'react-dom';
import qdata from './qdata.json'

function App() {



  const [number, setNum] = useState(Math.floor(Math.random() * 103))
  

  useEffect(() => {
    const script = document.createElement('script');
  
    script.src = "https://cdn.freecodecamp.org/testable-projects-fcc/v1/bundle.js";
    script.async = true;
  
    document.body.appendChild(script);
  
    return () => {
      document.body.removeChild(script);
    }
  }, []);

console.log(number)
function handleClick(){
 

  setNum(number +1)
  if (number == 101 ){
    console.log('number too high')
      setNum(number - 101)}
 

}



  return (
        <div id="quote-box">
          
        <img id="quotes" src="../image_store/logo-twitter-png-5862.png"></img>
        <img id="quotes" src="../image_store/2.png"></img>

      <div id="text">
      {(qdata.quotes[number].quote)}

      </div>
      <div id="author">
      {(qdata.quotes[number].author)}
      </div>
      <button id="new-quote" onClick={handleClick} >New Quote</button>
      <a href={`https://twitter.com/intent/tweet/?text=${(qdata.quotes[number].quote)}`} id="tweet-quote">Tweet</a>
      
    </div>
  );
}

export default App;
